import {
  FC,
  MouseEvent,
  ReactNode,
  useCallback,
  useMemo,
  useState,
} from 'react';

// Components
import ExpandableSection from '../ExpandableSection';
import Icon from '../Icon';
import { ContainerProps } from '../Container';
import Button from '../Button';

// Types
import { TestMetadata } from 'types';

// Styles
import './CollapsibleControl.scss';

export interface CollapsibleControlProps {
  /** Elements in the expanded section */
  children?: ReactNode;
  /** Class name */
  className?: string;
  /** Override props for container (minus children) */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Function for when the control is clicked */
  onClick?: (nextState: boolean) => void;
  /** Should collapsible control be expanded by default */
  defaultIsExtended?: boolean;
  /** Sets test id */
  testMetadata?: {
    collapsibleControl?: TestMetadata;
    wrapper?: TestMetadata;
    mainSection?: TestMetadata;
    expandedSection?: TestMetadata;
  };
}

const CollapsibleControl: FC<CollapsibleControlProps> = ({
  children = 'Collapse',
  containerProps = {
    padding: 'normal',
    fill: 'theme',
  },
  onClick,
  defaultIsExtended = true,
  testMetadata,
  className,
}) => {
  const [isSectionExpanded, setIsSectionExpanded] =
    useState<boolean>(defaultIsExtended);

  const collapsibleMainSection = useMemo(() => {
    const collapseArgs = {
      altText: 'Expand Section',
      iconName: 'fa-angle-double-right',
      title: 'Expand Section',
    };

    const expandArgs = {
      altText: 'Collapse Section',
      iconName: 'fa-angle-double-left',
      title: 'Collapse Section',
    };

    return (
      <Icon
        faPackage="fas"
        iconType="fa"
        className="sb-expand-collapse__icon"
        {...(isSectionExpanded ? expandArgs : collapseArgs)}
      />
    );
  }, [isSectionExpanded]);

  const handleSectionClick = useCallback(
    (e: MouseEvent<HTMLElement>, currentlyExpanded: boolean) => {
      setIsSectionExpanded(!currentlyExpanded);
      e.preventDefault();

      if (onClick !== undefined) {
        onClick(!currentlyExpanded);
      }
    },
    [onClick],
  );

  return (
    <Button
      onClick={(e) => handleSectionClick(e, isSectionExpanded)}
      {...testMetadata?.collapsibleControl}
      className="sb-collapsible-control"
      styleType="link"
    >
      <ExpandableSection
        isExpanded={isSectionExpanded}
        mainSection={collapsibleMainSection}
        containerProps={containerProps}
        testMetadata={testMetadata}
        className={className}
      >
        {children}
      </ExpandableSection>
    </Button>
  );
};

export default CollapsibleControl;
